import React, { useEffect, useState } from 'react'
require('dotenv').config()

const ContactForm = () => {
    // const [data, setData] = useState([]);
    const [name, setName] = useState("");
    const [subject, setSubject] = useState("");
    const [email, setEmail] = useState("");
    const [message, setSetMessage] = useState("");

    const handleSubmit = (e) => {
        e.preventDefault();
        const requestOptions = {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ name: name, subject: subject, email: email, message: message })
        };
        
        fetch(`/contactform`, requestOptions)
          .then((response) => response.json())
          .then(data =>  alert(`Thanks for your Submission ${name} `))
          .then(data =>  setName('')
          .then(data =>  console.log('Datat', data)),
          setSubject(''),
          setEmail(''),
          setSetMessage(''))
    
      };


    return (
        <div class="col-12 col-lg-7 wow fadeInLeft">
              <h4 class="heading text-center text-lg-left">GET IN TOUCH</h4>
              <form class="row contact-form" id="contact-form-data" onSubmit={handleSubmit} >
                <div class="col-sm-12" id="result"></div>
                <div class="col-12 col-md-5">
                  <input
                    type="text"
                    name="userName"
                    placeholder="Your Name"
                    class="form-control"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                  />
                  <input
                    required
                    type="email"
                    name="userEmail"
                    placeholder="Email Address *"
                    class="form-control"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                  <input
                    type="text"
                    name="userSubject"
                    placeholder="Subject"
                    class="form-control"
                    value={subject}
                    onChange={ (e) => { setSubject(e.target.value)}}
                  />
                </div>
                <div class="col-12 col-md-7">
                  <textarea
                    class="form-control"
                    name="userMessage"
                    rows="6"
                    placeholder="Your Message"
                    value={message}
                    onChange={ (e) => { setSetMessage(e.target.value)}}
                  ></textarea>
                </div>
                <div class="col-12">
                  <button
                    onClick={handleSubmit}
                    class="btn standard-btn rounded-pill w-100 contact_btn"
                  >
                    <i
                      class="fa fa-spinner fa-spin mr-2 d-none"
                      aria-hidden="true"
                    ></i>
                    Send Message
                  </button>
                </div>
              </form>
            </div>
    )
}


export default ContactForm;